import React, { useEffect, useState } from 'react'
import axios from 'axios'
import ListItem from '../components/ListItem'
import './List.css';
import SwipeableViews from 'react-swipeable-views';

const List = () => {
    const [list, setList] = useState([])
    const [isLoading, setIsLoading] = useState(true)

    const getList = async () => {
        // const { data: listData } = await axios.get("http://dindoo.cafe24app.com/insta/list/all")
        const { data : listData } = await axios.get("https://dindoo.herokuapp.com/insta/list/all",{headers: {"X-Requested-With": "XMLHttpRequest"}})  // CORS에러 -> 된다된다된다!!!!!!! ==> cors-everywhere 로 다시 해결
        setList(listData)
        setIsLoading(false)
    }

    useEffect(() => { getList() }, [])
    
    return (
        <div className="list-container">
            <SwipeableViews enableMouseEvents>
            {
                isLoading ? <div>Loading...</div> : list.map((item, index) => <ListItem id={item.id} key={index} content={item.content} image={item.image} name={item.name} avatar={item.avatar} />)
            }
            </SwipeableViews>
        </div>
    )

}

export default List